import React, { useState, useRef } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import Button from '../../components/atoms/Button/Button';
import { LoginWrapper, TitleText, InputForm, Label, Input, ErrorP } from '../EmailLogin/styled';
import useInput from '../../hooks/use-Input.js';

//      ->  api ?
export const JoinMembership = () => {
    const [msg, setMsg] = useState(''); // 
    const [msgPw, setMsgPw] = useState(' 6  .'); //  
    const emailInputRef = useRef(); //  
    const passwordInputRef = useRef(); //  

    const navigate = useNavigate();

    const emailRegex =
        /([\w-.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/;

    const {
        value: enteredEmail, //   email
        isValid: enteredEmailIsValid, //   
        changeHandler: emailChangeHandler, //    
        blurHandler: emailBlurHandler, //  ?
        reset: resetEmailInput, //    ?
    } = useInput(value => emailRegex.test(value));

    const {
        value: enteredPassword,
        isValid: enteredPasswordIsValid,
        changeHandler: passwordChangeHandler,
        blurHandler: passwordBlurHandler,
        reset: resetPasswordInput,
    } = useInput(value => value.length >= 6);

    let formIsValid = false; // form  

    if (enteredEmailIsValid && enteredPasswordIsValid) {
        formIsValid = true;
    }

    // API 
    const formSubmitHandler = async e => {
        e.preventDefault();

        //         ?
        if (!enteredEmailIsValid || !enteredPasswordIsValid) {
            //   false  false  
            return; //   true or false  ?
        }

        //      enteredEmailValue, enteredPasswordValue 
        const enteredEmailValue = emailInputRef.current.value;
        const enteredPasswordValue = passwordInputRef.current.value;

        console.log(enteredEmailValue);

        //   &  
        const userJoinData = {
            user: {
                email: enteredEmailValue,
                password: enteredPasswordValue,
            },
        };

        try {
            await axios
                .post(`${process.env.REACT_APP_BASE_URL}/user/emailvalid`, userJoinData, {
                    headers: {
                        'Content-type': 'application/json',
                    },
                })
                .then(res => {
                    const result = res.data;

                    //    
                    if (result.message === '   .') {
                        setMsg('*    .'); //     .

                        if (!enteredEmailIsValid) {
                            setMsg('*   .');
                        } else if (!enteredPasswordIsValid) {
                            setMsgPw('*  6  .');
                        } else {
                            setMsg(result.message); // .
                            navigate('/join/profileSetting', {
                                //    
                                state: {
                                    email: enteredEmailValue,
                                    password: enteredPasswordValue,
                                },
                            });
                        }
                    } else if (result.message === '    .') {
                        setMsg(result.message);
                    }
                });
        } catch (error) {
            navigate('/notfound');
        }

        //    ,   
        resetEmailInput();
        resetPasswordInput();
    };

    return (
        <LoginWrapper className="login-wrap">
            <TitleText className="title"> </TitleText>
            <form onSubmit={formSubmitHandler}>
                <InputForm>
                    <Label htmlFor="email"></Label>
                    <Input
                        type="email"
                        id="userMail"
                        value={enteredEmail}
                        onBlur={emailBlurHandler}
                        onChange={emailChangeHandler}
                        ref={emailInputRef}
                        placeholder="   ."
                        required
                    />
                    {{ msg } && <ErrorP>{msg}</ErrorP>}
                </InputForm>

                <InputForm>
                    <Label htmlFor="pw"></Label>
                    <Input
                        type="password"
                        id="userPassword"
                        value={enteredPassword}
                        onBlur={passwordBlurHandler}
                        onChange={passwordChangeHandler}
                        ref={passwordInputRef}
                        placeholder="  ."
                        required
                    />
                    {/*   ,  ,  6     , 6    */}
                    {enteredPassword.length >= 1 && enteredPassword.length < 6 ? <ErrorP>{msgPw}</ErrorP> : ''}
                </InputForm>
                <Button size="large" type="submit" disabled={!formIsValid}>
                    
                </Button>
            </form>
        </LoginWrapper>
    );
};

export default JoinMembership;
